import { usePostIds, usePosts } from "../hooks/posts";
import { Post } from "./Post";

import style from "../style/post.module.sass";
import { useState, useEffect } from "react";



export function PostList(props) {
    var postIds = usePostIds(props.rerender);
    var posts = usePosts(postIds);
    var refs = {};

    var postObjects = posts.map((post) => {
        return (<Post key={post.entity.id} post={post} ref={(ref) => refs[post.entity.id]=ref}></Post>)
    });
    console.log(refs);

    useEffect((eee) => {
        console.log("scroll")
        console.log(props.rerender)
        //console.log(JSON.stringify(refs));
        if(refs[props.rerender]) {
            console.log("scrolling")
            refs[props.rerender].scrollTo({
                top: 50,
                left: 500,
                behavior: 'smooth'
            })
        }
    }, [postIds, refs]);

    // <button onClick={() => {window.supersecrettrashusers()}}>Accs</button>
    return (
        <div className={style.postList}>
            
            {postObjects}
        </div>
    );
}